    <script>
    import {
        defineComponent,
    } from '@vue/composition-api'
    import {
        extend,
        ValidationObserver,
        ValidationProvider
    } from 'vee-validate';
    import {
        ConvertCustomFieldIntoDynamicFormObj,
        ConvertDynamicFormObjToCustomFieldObj
    } from "@/assets/common/vue-common.js"
    import DataService from '../../services/DataService';
    import moment from 'moment'
    extend('required', {
        validate(value) {
            console.log("checkk")
            return {
                required: true,
                valid: ['', null, undefined].indexOf(value) === -1
            };
    
        },
        message: 'The {_field_} is required'
        ///computesRequired: true
    });
    export default defineComponent({
       // name: "Manage Departemts",
        components: {
            ValidationObserver,
            ValidationProvider
        },
        props: {
            addApiUrl: {
                type: String
            },
            emitName: {
                type: String
            },
            moduleName: {
                type: String
            },
            subModule: {
                type: String
            },
            title: {
                type: String
            },
            recordId: {
                type: Number
            },
            addUserGuide:{
                type: String
            }
        },
    
        created: function () {
            let id = this.recordId ? this.recordId : 0
            this.getForm(id)
            if (this.moduleName == 'Review' && this.subModule == 'PerformanceReview'){
                 this.GetReviewTemplateData();
            }
        },
        watch: {
            hoursDifference() {
                const dynamicFormElement = this.$refs.dynamicForm.$el;
                const shiftHoursInput = dynamicFormElement.querySelector('#shift_hours input[type="text"]');
                if (shiftHoursInput) {
                    shiftHoursInput.value = this.hoursDifference;
                }
            }
        },
        methods: {
            getDifference() {
                if (this.startTime && this.endTime) {
                    const startParts = this.startTime.split(':');
                    const endParts = this.endTime.split(':');

                    let startHour = parseInt(startParts[0]);
                    let endHour = parseInt(endParts[0]);
                        
                    // Convert PM times to 24-hour format
                    if (startParts[1].includes('PM') && startHour !== 12) {
                        startHour += 12;
                    }
                    if (endParts[1].includes('PM') && endHour !== 12) {
                        endHour += 12;
                    }
                    
                    const startMinutes = startHour * 60 + parseInt(startParts[1]);
                    const endMinutes = endHour * 60 + parseInt(endParts[1]);
                    
                    let differenceMinutes = endMinutes - startMinutes;
                    if (differenceMinutes < 0) {
                        differenceMinutes += 24 * 60; // Add 24 hours if end time is before start time
                    }
                    
                    let hours = Math.floor(differenceMinutes / 60);
                    let minutes = differenceMinutes % 60;

                    this.hoursDifference = parseFloat(hours + '.' + minutes);
                } else {
                // Return a message indicating that both start time and end time are required
                return 'Start time and end time are required to calculate the difference.';
                }
            },
            getForm: function (id) {
                var ths = this;
                ths.isLoading = true;
                var url = `recordId=${id}&moduleName=${this.moduleName}&subModuleCode=${this.subModule}`;
                DataService.GetDynamicForm(url).then(function (response) {
                    if (response != null && response.data != null) {
                        ths.isMapFeature = response.data.isFsmLocating;
                        // response.data.data                    
                        ths.FormSchema = ths.$refs.dynamicForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);

                        if(ths.moduleName == 'Company' && ths.subModule == 'CompanyHoliday'){
                            ths.FormSchema.push({groupId:null,group_name:"",layoutType:'single',visibility:true,
                            Data:[
                                    {
                                        astype: "SlotField",
                                        label: '',
                                        name: "AvailableClientsLang",
                                        value: "",
                                        visibility: true,
                                        placeholder: "",
                                        config: {
                                            options: [],
                                            showAddIcon: false,
                                        },
                                        validationRules: "",
                                    }
                                ]
                            })
                        }else if (ths.moduleName == 'Review' && ths.subModule == 'PerformanceReview'){
                            ths.FormSchema.push({groupId:null,group_name:"",layoutType:'single',visibility:true,
                            Data:[
                                    {
                                        astype: "SlotField",
                                        label: ths.$t('Question'),
                                        name: "ManageQuestionaire",
                                        value: "",
                                        visibility: true,
                                        placeholder: "",
                                        config: {
                                            options: [],
                                            showAddIcon: false,
                                        },
                                        validationRules: "",
                                    }
                                ]
                            })
                            ths.ManageQuestionaire = true;
                        }

                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                // if (group.group_name == "General Information" || group.group_name == "GENERAL INFORMATION") {
                                //     group.visibility = false;
                                    
                                // }
                                if (field.name == "user_name" || field.name == "password" || field.name == "signature" || field.name == "from_time" || field.name == "to_time") {
                                    field.visibility = false;
                                }
                                if (field.astype == "RadioField") {
                                    field.value = 1;
                                }
                                if (field.name == "break_duration") {
                                    field.config.is24hr = true;
                                    //ths.$refs.dynamicForm.updateProperty("break_duration", "config.is24hr", true);
                                    //ths.$refs.dynamicForm.UpdateKeyValue();
                                    //ths.$refs.dynamicForm.updateProperty("break_duration", "config.format", "hh:mm A");
                                }

                                // ths.$refs.AddTicketForm.UpdateKeyValue();
                                // if (field.name == "break_duration") {
                                //     field.config.format ="hh:mm A"
                                // }

                                if (field.name == "attachment") {
                                    field.config = {
                                        maxSize: 5,
                                        accept: [
                                            {
                                                fileType: 'doc', // Document type
                                                iconClass: 'fa-file-word-o' // Icon class for documents
                                            },
                                            {
                                                fileType: 'docx', // Document type
                                                iconClass: 'fa-file-word-o' // Icon class for documents
                                            },
                                            {
                                                fileType: 'txt', // Text file type
                                                iconClass: 'fa-file-text-o' // Icon class for text files
                                            },
                                            {
                                                fileType: 'jpg', // Image types
                                                iconClass: 'fa-file-image-o' // Icon class for images
                                            },
                                            {
                                                fileType: 'jpeg', // Image types
                                                iconClass: 'fa-file-image-o' // Icon class for images
                                            },
                                            {
                                                fileType: 'png', // Image types
                                                iconClass: 'fa-file-image-o' // Icon class for images
                                            },
                                            {
                                                fileType: 'gif', // Image types
                                                iconClass: 'fa-file-image-o' // Icon class for images
                                            },
                                            {
                                                fileType: 'bmp', // Image types
                                                iconClass: 'fa-file-image-o' // Icon class for images
                                            },
                                            // {
                                            //     fileType: 'mp4', // Video types
                                            //     iconClass: 'fa-file-video-o' // Icon class for videos
                                            // },
                                            // {
                                            //     fileType: 'mov', // Video types
                                            //     iconClass: 'fa-file-video-o' // Icon class for videos
                                            // },
                                            // Add more image, document, text, and video types as needed
                                        ],
                                        multiple: false,
                                    }
                                }

                                if (field.name == "shift_hours") {
                                    field.disabled = true;
                                    field.placeholder = "Shift Hours"
                                }
                                
                                if(field.name == "startdate"){
                                    field.config.minDate = new Date() ;
                                }
                                
                                field.config.onChange = ths.OnChangeEventsforField;
                                //ths.$refs.dynamicForm.updateProperty("break_duration", "config.format", "hh:mm A");
                            });
                        });
                        console.log(ths.FormSchema);
                        if (ths.FormSchema.length > 0) {
                            ths.FormSchema[0].visibility = false
                        }
                        ths.isLoading = false;
                    }
                });
            },

            GetStartData: function (event, id) {
                var vueObj = this;
                (vueObj.FormSchema)[2].Data[1].config.minDate = event;
            },

            OnChangeEventsforField: function (x, y, z) {
                let ths = this;
                if (x == "FILL_DEPENDENT") {
                    y.config.options = [];
                    var url = `id=&moduleName=${ths.moduleName}&type=${y.config.fieldCode}&search=&refId=${z}`;
                    DataService.GetDDLData(url).then(function (response) {
                        if (response.data) {
                            var json = response.data;
                            if (json != null) {
                                y.config.options = response.data.DATA;
                                ths.$refs.dynamicForm.UpdateKeyValue();
                            }
                        }
                    });
                } else {
                    if(y.name == "payment_gateway_code" && y.config.options.filter((i) => i.value == y.value)[0].name == "Authorize") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "user_name") {
                                    field.visibility =  true;
                                    field.label = "API Login ID"
                                }
                                if(field.name == "password") {
                                    field.visibility =  true;
                                    field.label = "Transaction Key"
                                }
                                if(field.name == "signature") {
                                    field.visibility =  false;
                                }
                            });
                        });
                    } else if (y.name == "payment_gateway_code" && y.config.options.filter((i) => i.value == y.value)[0].name == "Paypal") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "user_name") {
                                    field.visibility =  true;
                                    field.label = "User Name"
                                }
                                if(field.name == "password") {
                                    field.visibility =  true;
                                    field.label = "Password"
                                }
                                if(field.name == "signature") {
                                    field.visibility =  true;
                                }
                            });
                        });
                    } else {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "user_name") {
                                    field.visibility =  true;
                                    field.label = "Secret Key"
                                }
                                if(field.name == "password") {
                                    field.visibility =  true;
                                    field.label = "Publishable Key"
                                }
                                if(field.name == "signature") {
                                    field.visibility =  false;
                                }
                            });
                        });
                    }
                    if(y.name == "attachment") {
                        ths.GetBase64String(y.value);
                    }
                    if(y.name == "start_time") {
                        ths.startTime = ths.$options.filters.formatTime(y.value);
                        ths.getDifference();
                    }
                    if(y.name == "end_time") {
                        ths.endTime = ths.$options.filters.formatTime(y.value);
                        ths.getDifference();
                    }
                    if(y.name == "location_country") {
                        console.log("country", y.value);
                        this.GetStateDDL(y.value);
                    }
                    if(y.name == "is_full_day" && y.config.options.filter((i) => i.value == y.value)[0].name == "Partial Day") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "from_time") {
                                    field.visibility =  true;
                                }
                                if(field.name == "to_time") {
                                    field.visibility =  true;
                                }
                            });
                        });
                    } else {
                        if(y.name == "is_full_day" && y.config.options.filter((i) => i.value == y.value)[0].name != "Partial Day") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "from_time") {
                                    field.visibility =  false;
                                }
                                if(field.name == "to_time") {
                                    field.visibility =  false;
                                }
                            });
                        });
                      }
                    }

                    if(y.name == "category_type_id" && y.config.options.filter((i) => i.value == y.value)[0].name == "Ticket") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "IsForServiceRequest") {
                                    field.visibility =  true;
                                }                              
                                
                            });
                        });
                    } 
                    else{
                        if(y.name == "category_type_id" && y.config.options.filter((i) => i.value == y.value)[0].name != "Ticket") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "IsForServiceRequest") {
                                    field.visibility =  false;
                                }                              
                                
                            });
                        });
                      } 
                    } 

                    if(y.name == "isFixed" && y.config.options.filter((i) => i.value == y.value)[0].name == "Observed") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "is_recurring") {
                                    field.value =  0;
                                    field.disabled = true;
                                }
                            });
                        });
                    } else {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if(field.name == "is_recurring") {
                                    field.disabled = false;
                                }
                            });
                        }); 
                    }

                    if(y.name == "startdate"){                           
                        ths.$refs.dynamicForm.updateProperty("enddate", "config.minDate", x);
                        ths.$refs.dynamicForm.UpdateKeyValue();                      
                    }

                    if(y.name == "IsForCompany"){
                        const checkvalueexist =  y.value.find(x=> x == 0);
                        if(checkvalueexist != undefined){
                            ths.showContractDiv = true;
                        }else{
                            ths.showContractDiv = false;
                        }
                    }
                    
                }
                
            },
            GetBase64String: function (evt) {
            try {
                var vm = this;
                //alert(vm.apiurl);
                vm.fileData = [];

                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                // Closure to capture the file information.
                reader.onload = (function (theFile) {

                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64

                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);

                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;

                        // var ggg = vm.configData;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };
                        console.log(fileObj);
                        vm.fileData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
            },

            SizeToMb: function (bytes) {
                var vueObj = this;
                var sizeInBuytes = parseInt(vueObj.allowedPerFileSizeInMb, 10) * 1048576;
                if (bytes > sizeInBuytes) {
                    return -1;
                }
                else {
                    var byesInMB = (bytes / 1024) / 1024;
                    return byesInMB;
                }
            },
        
