            GetRadioValue(data,event,getid){
                var vm = this;
                
                if(data.value != undefined){
                    var array = document.getElementsByTagName("input");  
                    for(var i = 0; i < array.length; i++){
                        if(array[i].type == "radio"){
                            if(getid.includes('radiovisibility_')){
                                if(array[i].id == getid){
                                    array[i].value = data.value;
                                    vm.modelQuestionVisibility.push(data);
                                    break;
                                }
                            }else if (getid.includes('radio_')){
                                if(array[i].id == getid){
                                    array[i].value = data.value;
                                    vm.modelStars.push(data);
                                    break;
                                }else{
                                    array[i].value = data.value;
                                }
                                
                            }
                        }
                    }
                }
                // else{
