            SelectAllChk(data){
                if(data == 'ChkMainItems'){
                    var array = document.getElementsByTagName("input");  
                    for(var i = 0; i < array.length; i++){
                        if(array[i].type == "checkbox"){
                            if(array[i].className == 'deletable custom-control-input'){
                                var getScreenshotids = document.getElementsByClassName('masterchk custom-control-input ')
                                if(getScreenshotids[0].checked){
                                    array[i].checked = true;
                                }else{
